import React, { Component } from 'react'
import { connect } from 'react-redux'
import { fromJS } from 'immutable';

import LazyImg from 'Extended/LazyImg/LazyImg';
import styles from './LazyList.scss';

class LazyList extends Component {
    static defaultProps = {
        placeholder: [0, 1, 2, 3, 4, 5],
    }
    componentWillMount() {
        this.handleReqData(this.props)
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.curcity.get('key') !== this.props.curcity.get('key')) {
            this.props.dispatch('news/save', { list: [] })
            this.handleReqData(nextProps)
        }
    }
    async handleReqData(props) {
        let city = props.curcity.get('key')
        let res = await apireq('api/list/' + city)
        if (res.success) {
            this.props.dispatch('news/save', { list: res.data })
        }
    }
    handleMoreData = async () => {
        let { curcity, list } = this.props
        let city = curcity.get('key')
        let res = await apireq('api/list/' + city)
        if (res.success) {
            list = list.concat(fromJS(res.data))
            this.props.dispatch('news/save', { list })
        }
    }
    render() {
        let { list, _viewport, placeholder } = this.props
        return (
            list && list.size > 0 ?
                [list.map((item, index) => {
                    return (
                        <div className={styles.item} key={index} >
                            <div className={styles.title}>
                                <div className={styles.tit}>{item.get('title')}</div>
                                <div className={styles.dec}>{item.get('createdAt')}</div>
                            </div>
                            <LazyImg src={item.get('image')} className={styles.img} alt={item.get('title')} {..._viewport} />
                        </div>
                    )
                }),
                <MoreDatas {..._viewport} />] :
                [placeholder.map(pld => {
                    return (
                        <div className={styles.item} key={pld} >
                            <div className={styles.title}>
                                <div className={styles.tit}></div>
                                <div className={styles.dec}></div>
                            </div>
                            <LazyImg className={styles.img} />
                        </div>
                    )
                })]
        )
    }
}

export default connect((state) => ({
    curcity: state.getIn(['init', 'curcity']),
    list: state.getIn(['news', 'list']),
}))(LazyList)


class MoreData extends Component {
    static defaultProps = {
        _fwb: 50,
        currentpage: 1,
    }
    constructor(props) {
        super(props)
        this.state = {
            loading: false,
            loaderror: false,
            currentpage: props.currentpage,
        }
    }
    componentWillReceiveProps(nextProps) {
        if (!this.state.loading && !this.state.loaderror && this.props._fwb) {
            this.handleReqMoreData()
        }
    }
    handleReqMoreData = async () => {
        let { top, height } = this.ele.getBoundingClientRect();
        let { _vph, _fwb } = this.props
        if ((top + height + _fwb) >= 0 && top <= (_vph + _fwb)) {
            this.handleReqData()
        }
    }
    handleReqData = async () => {
        this.setState({
            loading: true,
            loaderror: false,
        })
        let nextpage = this.state.currentpage + 1
        let { curcity, list } = this.props
        let city = curcity.get('key')
        let res = await apireq('api/list/' + city, {
            params: {
                currentpage: nextpage
            }
        })
        if (res.success) {
            list = list.concat(fromJS(res.data))
            this.props.dispatch('news/save', { list })
            this.setState({
                loading: false,
                currentpage: nextpage,
            })
        } else {
            this.setState({
                loading: false,
                loaderror: true,
            })
        }
    }
    render() {
        let { loading, loaderror } = this.state
        return (
            loaderror ?
                <div className={styles.loadmore} onTouchStart={this.handleReqData} >加载数据出错了，请点击重试</div> :
                <div className={styles.loadmore} ref={ele => this.ele = ele}>{loading ? '数据正在加载中...' : '数据加载完成'}</div>
        )
    }
}

const MoreDatas = connect((state) => ({
    curcity: state.getIn(['init', 'curcity']),
    list: state.getIn(['news', 'list']),
}))(MoreData)


